import React from "react";

export default function ImgContainer(props) {
  return (
    <div
      className="imgSort"
      style={{
        margin: `${props.margin}px auto`,
        justifyContent: props.justify,
      }}
    >
      {props.images.map((obj, i) => (
        <img src={obj.img} key={i} width={obj.width} alt={`${obj.img}`} />
      ))}
    </div>
  );
}
